/*!
*
* app
*
!*/
body {
  background-color: #fff;
  margin-bottom: 100px;
}
h1,
h2,
h3,
h4,
h5,
h6,
dt,
label {
  font-family: 'Roboto Condensed';
  font-weight: 300;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  line-height: 1;
}
h1 {
  font-weight: 300;
  margin: 0 0 .25em 0;
  padding: 0 0 .25em 0;
  font-size: 18pt;
  font-size: 6vw;
}
@media (max-width: 480px) {
  h1 {
    font-size: 16pt;
  }
}
@media (min-width: 992px) {
  h1 {
    font-size: 42pt;
  }
}
h2 {
  padding: 0 0 .25em 0;
  font-size: 16pt;
  font-size: 4.5vw;
}
@media (max-width: 480px) {
  h2 {
    font-size: 14pt;
  }
}
@media (min-width: 992px) {
  h2 {
    font-size: 24pt;
  }
}
h2 a .fa.fa-external-link {
  font-size: 50%;
  margin-left: .5em;
}
h3 {
  padding: 0 0 .25em 0;
  font-size: 14pt;
  font-size: 3vw;
}
@media (max-width: 767px) {
  h3 {
    font-size: 12pt;
  }
}
@media (min-width: 992px) {
  h3 {
    font-size: 20pt;
  }
}
p {
  max-width: 48em;
  line-height: 1.4em;
  margin-bottom: 1em;
}
p.indent {
  text-indent: 1.5em;
}
code {
  word-wrap: break-word;
}
a {
  cursor: pointer;
}
.navbar-default {
  margin: 0 0 40px 0;
  min-width: 240px;
}
.navbar-default .navbar-toggle:hover,
.navbar-default .navbar-toggle:focus,
.navbar-default .navbar-collapse,
.navbar-default {
  background-color: #00bce3;
  border: none;
  border-radius: 0;
}
.navbar-default .navbar-toggle {
  border: none;
}
.navbar-default .navbar-toggle:hover {
  background-color: transparent;
}
.navbar-default .navbar-toggle .icon-bar {
  background-color: rgba(255, 255, 255, 0.6);
}
.navbar-default .navbar-brand {
  font-size: 20pt;
  line-height: 60px;
  padding: 0 20px;
  margin: 0;
  padding-left: 2vw;
  font-weight: 300;
}
.navbar-default .navbar-nav {
  margin: 0;
  padding: 0 20px 0 0;
  padding-right: 2vw;
}
.navbar-default .navbar-nav > li {
  margin: 0 1em;
  padding: 0;
}
.navbar-default .navbar-nav > li > a {
  padding: 0;
  margin: 0;
  color: #fff;
  display: block;
  text-align: center;
  font: 300 14pt/60px Roboto Condensed;
  width: 100%;
  opacity: .8;
  transition: all 150ms ease-in;
}
@media (min-width: 600px) {
  .navbar-default .navbar-nav > li > a {
    display: inline-block;
  }
}
.navbar-default .navbar-nav > li > a i.fa {
  margin-right: .25em;
}
.user-controls .avatar-selector {
  margin: 1em 0 0;
}
.user-controls .avatar-selector img.avatar-option {
  width: 128px;
  height: 128px;
  max-width: 15vw;
  cursor: pointer;
  border: 2px dashed transparent;
}
.user-controls .avatar-selector img.avatar-option.active {
  border-color: #999;
}
.friends-manager {
  margin: 2em 0;
}
.friends-manager .nav-tabs li .nav-label {
  display: none;
}
@media (min-width: 600px) {
  .friends-manager .nav-tabs li .nav-label {
    display: inline-block;
  }
}
.friends-manager .nav-tabs i.fa {
  margin: 0;
}
@media (min-width: 600px) {
  .friends-manager .nav-tabs i.fa {
    margin-right: .5em;
  }
}
.friends-manager .tab-panels {
  padding: 1em;
}
.friends-manager .tab-panels .tab-content {
  display: none;
}
.friends-manager .tab-panels .tab-content.active {
  display: block;
}
.users-list {
  font-size: 14pt;
  font-weight: 400;
  line-height: 32px;
  margin: .5em 0;
}
.users-list .friend-actions {
  padding-right: .5em;
}
.users-list .friend-actions .friend-action {
  padding: 0 .4em;
}
.users-list img.avatar {
  height: 32px;
  margin-right: .5em;
}
